 -->
<template>
  <login-bg>
    <div class="body">
      <h3 class="form-title">
        忘记密码
        <div class="line"></div>
      </h3>
      <el-form :model="formFields"
        :rules="rules"
        ref="form">
        <el-form-item prop="accountNo">
          <el-input placeholder="请输入账号"
            v-model.trim="formFields.accountNo"
            maxlength="31" />
        </el-form-item>

        <el-form-item prop="idCard">
          <el-input placeholder="请输入身份证号"
            v-model.trim="formFields.idCard"
            maxlength="18" />
        </el-form-item>

        <el-form-item prop="passwords">
          <el-input placeholder="8~16位，至少包含数字/字母/字符两种组合"
            v-model="formFields.passwords"
            autocomplete="new-password"
            type="password"
            maxlength="17" />
        </el-form-item>

        <el-form-item prop="confirmPwd">
          <el-input placeholder="确认密码"
            v-model="formFields.confirmPwd"
            autocomplete="new-password"
            type="password"
            maxlength="17" />
        </el-form-item>
        <a class="a-btn"
          @click="handleForgerPwd"
          :class="{'loading': loading}">
          <i class="el-icon-loading"
            v-if='loading'></i>
          {{loading ? '加载中...' : '确 认'}}
        </a>
      </el-form>
    </div>
  </login-bg>
</template>

<script>
import { lenRule, regIdNo } from '@/lib/utils'
import LoginBg from './LoginBg.vue'
export default {
  name: 'ForgetPwd',
  components: { LoginBg },
  data() {
    return {
      formFields: {
        accountNo: '',
        idCard: '', // 421087199008234746
        passwords: '',
        confirmPwd: ''
      },
      rules: {
        accountNo: [{ required: true, message: '请输入账号', trigger: 'blur' }, ...lenRule(30)],
        idCard: [
          { required: true, message: '请输入身份证号码', trigger: 'blur' },
          {
            pattern: regIdNo,
            message: '请输入正确的身份证号码',
            trigger: 'blur'
          }
        ],
        passwords: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          {
            validator(rule, val, cb) {
              if (/^\d+$/.test(val)) return cb(new Error('密码不能为纯数字'))
              if (/^[a-zA-Z]+$/.test(val)) return cb(new Error('密码不能为纯字母'))
              if (/^[^0-9A-Za-z]+$/.test(val)) return cb(new Error('密码不能为纯符号'))
              return cb()
            },
            trigger: 'blur'
          },
          { min: 8, max: 16, message: '密码长度为8~16位', trigger: 'blur' },
          { max: 16, message: '密码长度为8~16位' }
        ],
        confirmPwd: [
          { required: true, message: '请输入确认密码', trigger: 'blur' },
          {
            validator: (rule, val, cb) => {
              return this.formFields.passwords === val ? cb() : cb(new Error(rule.message))
            },
            message: '两次密码不一致',
            trigger: 'blur'
          },
          { max: 16, message: '密码长度为8~16位' },
          { min: 8, max: 16, message: '密码长度为8~16位', trigger: 'blur' }
        ]
      },
      loading: false
    }
  },
  methods: {
    handleForgerPwd() {
      if (this.loading) return
      this.loading = true
      this.$refs.form.validate(valid => {
        if (!valid) return (this.loading = false)
        let { accountNo, passwords, idCard } = this.formFields
        this.$post(
          '/manageUser/forgetPasswords',
          {
            accountNo,
            passwords,
            idCard
          },
          'noToken'
        )
          .then(_ => {
            this.loading = false
            this.$message.success('设置密码成功')
            this.$router.push({ name: 'Login' })
          })
          .catch(err => {
            this.$message.error(err)
            this.loading = false
          })
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import './style.less';
</style>


